input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

body {
  display: flex;
  margin: 0;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: lightslategrey;
}

.container {
  display: grid;
  width: 350px;
  background-color: rgb(208, 215, 219);
  border: 3px solid #000000;
  border-radius: 20px;
  box-shadow: 0 4px rgb(44, 42, 42);
}

#title {
  font-weight: bold;
  line-height: 50px;
  text-align: center;
  border-radius: 15px;
  font-size: 16px;
  font-weight: 700;
}

#display {
  background-color: rgb(22, 60, 92);
  color: white;
  line-height: 50px;
  text-align: right;
  padding-right: 16px;
  border: 6px solid #000000;
  font-size: 20px;
  font-weight: 700;
}

#button-area {
  display: grid;
  grid-template-rows: repeat(4, 50px);
  grid-template-columns: repeat(4, auto);
  column-gap: 4px;
  padding: 4px;
}

#clear {
  background-color: rgb(216, 86, 86);
}

button {
  background-color: rgb(224, 229, 233);
  border: 1px solid #000000;
  width: 100%;
  margin: 2px 4px 4px 0px;
  border-radius: 14px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 1px 2px rgb(77, 69, 69);
}

button:hover {
  background-color: rgb(212, 215, 218);
}

button:active {
  box-shadow: 0 1px rgb(77, 69, 69);
  transform: translateY(3px);
}

/* .operator {
} */

.equals {
  display: grid;
  grid-template-rows: repeat(1, 50px);
  padding: 4px;
  border-radius: 15px;
}
